@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;600;700&display=swap);
* {
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
  margin: 0;
  padding: 0;
  text-decoration: none;
  color: white;
}

.btn-primario {
  text-align: center;
  border-radius: 3px;
  padding: 5px 20px;
  margin: 0 10px;
  font-weight: 600;
  border: 2px solid white;

  color: white;
  background: transparent;
}

.btn-secundario {
  text-align: center;
  border-radius: 3px;
  padding: 5px 20px;
  margin: 0 10px;
  font-weight: 600;
  border: 2px solid white;

  background: white;
  color:black;
}

.titulo {
  color:black;
  padding: 25px 0;
}

.cabecalho {
  background-color:black;
  display: flex;
  justify-content: space-between;
  padding: 0 15vw;
  height: 10vh;
  align-items: center;
}

.container {
  background: #EECDA3;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #EF629F, #EECDA3); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */


  min-height: 90vh;
  padding: 0px 15vw;
}

.conteudo {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.imagem-logo {
  height: 50px;
  width: 50px;
}

.imagem-icone {
  height: 25px;
  width: 25px;
}

.btn {
  margin: 15px auto 0px auto;
  display: block;
  border-radius: 20px;
  background-color:black;
  border: none;
  color: white;
  font-weight: 600;
  font-size: 14px;
  padding: 8px 20px;
  cursor: pointer;
}

.box {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: transparent;
  border-radius: 5px;
  backdrop-filter: blur(20px);
  box-shadow: 34px 34px 50px 50px rgba(0, 0, 0, 0.04);
  padding: 20px;
  width: 48%;
}

.saldo {
  font-weight: 700;
  font-size: 32px;
}

.detalhe {
  color:black;
  font-size: 24px;
}

@media (max-width: 800px) {
  .box {
    width: 95%;
    margin: 5px;
  }

  .conteudo {
    flex-direction: column;
  }
}

